import React from 'react';
import PropTypes from 'prop-types';

function finder__selectbox(props) {
    const randomNumber = Math.random()
        .toString(16)
        .slice(-4);

    const setFacet = e => {
        const newQuery = props.query,
            newValue = e.target.value;
        newValue
            ? (newQuery.facets[props.facet.meta] = newValue)
            : delete newQuery.facets[props.facet.meta];
        newQuery.startRank = 1;
        // if(props.facet.meta === 'G' && !newValue){
        //     delete newQuery.facets.rSub;
        // }
        props.facet.meta === 'G' && !newValue
            ? delete newQuery.facets.rSub
            : null;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    //filter out related subject
    const filterRSubject = s => {
        let rs = s.data.replace(new RegExp('"', 'g'), '');
        let rq = props.query.facets.G.replace(new RegExp('"', 'g'), '');
        let regex = new RegExp('^' + rq + '_');
        return regex.test(rs);
    };

    //check if the facets is related subject
    const relatedSubject =
        props.query.facets.G && props.facet.values.filter(filterRSubject);

    return (
        <fieldset className="finder__filter finder__selectbox">
            <div className="finder__select">
                <label
                    className="finder__select__overline"
                    htmlFor={`meta_${props.facet.meta}_orsand--${randomNumber}`}
                >
                    {props.facet.name}
                </label>
                <select
                    name={props.facet.name}
                    id={`meta_${props.facet.meta}_orsand--${randomNumber}`}
                    value={
                        props.query.facets[props.facet.meta]
                            ? props.query.facets[props.facet.meta]
                            : ''
                    }
                    onChange={e => setFacet(e)}
                >
                    <option value="">{`All ${props.facet.name}`}</option>
                    {relatedSubject && props.facet.meta === 'rSub'
                        ? relatedSubject.map(value => {
                              return (
                                  <option key={value.data} value={value.data}>
                                      {value.label}
                                  </option>
                              );
                          })
                        : props.facet.values.map(value => {
                              return (
                                  <option key={value.data} value={value.data}>
                                      {value.label}
                                  </option>
                              );
                          })}
                </select>
            </div>
        </fieldset>
    );
}

finder__selectbox.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    responseFacet: PropTypes.arrayOf(PropTypes.object),
    update: PropTypes.object,
};

export default finder__selectbox;
